<template>
  <div class="reviews">
    <div class="reviews__wrapper">
      <div class="reviews__container">
        <div class="reviews__slides">
          <slider
            ref="slide"
            :sliderClass="this.sliderClassTrack"
            @change="this.changeSlide"
          >
            <div :class="['reviews__col', `${index-1}`]" v-for="index in 5" :key="index">
              <CardReviews :cardReviewsClass="this.getCardReviewsClass(index-1)"/>
            </div>
          </slider>
        </div>
        <div class="reviews__nav">
          <div class="reviews__nav-dots">
            <div
              v-for="index in 5"
              @click="this.moveToSlide(index-1)"
              :key="index"
              :class="{
                ['reviews__nav-dots-item']: true,
                ['reviews__nav-dots-item_selected']: index-1 === this.currentSlide
              }"
            >
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CardReviews from '@/views/element/CardReviews.vue'
import Slider from '@/views/block/Slider.vue'

export default {
  components: {
    CardReviews,
    Slider
  },
  data () {
    return {
      sliderClassTrack: 'slider-main',
      currentSlide: 0,
      trackOffset: 0,
      widthElement: 300
    }
  },
  methods: {
    changeSlide (index, trackOffset, widthElement) {
      this.currentSlide = index
      this.trackOffset = trackOffset
      this.widthElement = widthElement
    },
    moveToSlide (index) {
      this.$refs.slide.moveTo(index)
    },
    getCardReviewsClass (index) {
      if (this.currentSlide === index) {
        return 'card-reviews'
      } else {
        return 'card-reviews_disable'
      }
    }
  }
}
</script>

